import { Component, OnInit } from "@angular/core";
import { ReportService } from "../report/services/report.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { DatePipe } from "@angular/common";
import { StaffService } from "../staff/services/staff.service";

@Component({
  selector: "app-report",
  templateUrl: "./report.component.html",
  styleUrls: ["./report.component.scss"]
})
export class ReportComponent implements OnInit {
  newreport;

  date = Date.now();

  submitting: boolean;

  staffList = [];

  constructor(
    public reportservice: ReportService,
    public router: Router,
    public activeRouter: ActivatedRoute,
    private datePipe: DatePipe,
    private staffservice: StaffService
  ) {
    this.newreport = {};
  }

  ngOnInit() {
    {
      let i: any;
      this.staffservice.getAgencystaff().subscribe(
        (data: any) => {
          for (i = 0; i < data.length; i++) {
            this.staffList.push({ name: data[i].name, user_id: data[i].id });
          }
          console.log("NAMESSSS IN LIST ", this.staffList);
          this.newreport.report = this.staffList;
        },
        error => {
          console.log(error);
        }
      );
    }
  }

  submit() {
    console.log("Submitted>>>>>>>>>", this.newreport);
  }
}
