import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { BaseService } from "app/shared/services/base.service";
import { environment } from "environments/environment";

// import { Staff } from "app/shared/models/staff.model";

@Injectable()
export class StaffService extends BaseService {
  constructor(public http: HttpClient) {
    super("");
  }

  getAgencystaff() {
    return this.http.get(
      "http://192.168.10.22:8000/admin/get-staff-list",
      this.getOptionsWithToken()
    );
  }

  addCheckin(data) {
    return this.http.post(
      "http://192.168.10.22:8000/admin/attendance/checkin",
      data,
      this.getOptionsWithToken()
    );
  }

  getCheckinList(data) {
    return this.http.post(
      "http://192.168.10.22:8000/admin/attendance/get-checkin-list",
      { date: data },
      this.getOptionsWithToken()
    );
  }

  addCheckout(id, data) {
    return this.http.post(
      "http://192.168.10.22:8000/admin/attendance/checkout/" + id,
      { check_out: data },
      this.getOptionsWithToken()
    );
  }
}
